}
.menu__mobile-search {
  width: 0;
}

@media (max-width: 1330px) {
  .header__container {
    max-width: 95vw;
    margin: 0 auto;
  }
  .menu__search {
    max-width: 20vw;
  }
}
@media (max-width: 1150px) {
  .menu__search {
    width: 0;
    height: 0;
    opacity: 0;
  }
  .menu__search.active {
    width: 15vw;
    height: 30px;
    opacity: 1;
  }
  .menu__mobile-search {
    width: 30px;
  }
  .menu__mobile-search.active {
    width: 0px;
  }
}
@media (max-width: 950px) {
  .header__logo {
    flex: 0 1 auto;
  }
  .header__burger {
    display: block;
    position: absolute;
    right: 0;
    width: 30px;
    height: 20px;
    z-index: 3;
  }
  .header__burger span {
    position: absolute;
    background: #000;
    left: 0;
    width: 100%;
    top: 9px;
    height: 2px;
  }
  .header__burger:before, .header__burger:after {
    transition: 0.9s;
    content: "";
    background: #000;
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
  }
  .header__burger:after {
    bottom: 0;
  }
  .header__burger.active:before {
    transition: 0.5s;
    content: "";
    transform: rotate(45deg);
    top: 9px;
  }
  .header__burger.active:after {
    transition: 0.5s;
    content: "";
    transform: rotate(-45deg);
    top: 9px;
  }
  .header__burger.active span {
    display: none;
  }
  .menu {
    position: absolute;
    top: 100px;
    right: -200%;
    z-index: 100;
    background: #fff;
    border: 1px solid #D4D2E3;
  }
  .menu__list {
    flex-direction: column;
  }
  .menu__search.active {
    margin-left: 0;
    display: inline-block;
    z-index: 300;
    width: 100vw;
  }
  .menu__mobile-search {
    margin-right: 3rem;
    min-width: 50px;
  }
  .menu.active {
    right: 0;
    padding: 24px 50px 50px 24px;
  }
}
